import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    enabledKeyboardControls: true
  },
  getters: {
    enabledKeyboardControls (state) {
      return state.enabledKeyboardControls
    }
  },
  mutations: {
    enableKeyboardControls (state, value) {
      state.enabledKeyboardControls = value
    }
  },
  actions: {

  }
})
